
.paginator {
    display: grid;
    grid-template-columns: repeat(12, 1fr) ; 
    margin: 0 auto;
    background-color:  rgb(233, 233, 233);
    border-radius: 3px/3px;
    width: 98%;
    
    
}

.pageNumber {
    border: 1px solid grey;
    border-radius: 3px/ 3px;
    margin: 1px;
    text-align: center;
    
}

.pageNumber.selectedPage {
    font-weight: bold;
    border: 1px solid black;
}
